  /* typing ...*/
  
  .user__typing {
    font-style: italic;
    font-weight: bold;
    margin-right: 3px;
  }
  
  .typing {
    width: 5em;
    height: 2em;
    position: relative;
    padding: 10px;
    margin-left: 5px;
    background-color: #d32020;
    border-radius: 20px;
  }
  
  .typing__dot {
    float: left;
    width: 8px;
    height: 8px;
    margin: 0 4px;
    background-color: #8d8c91;
    border-radius: 50px;
    opacity: 0;
    animation: loadingFade 1s infinite;
  }
  
  .typing__dot:nth-child(1) {
    animation-delay: 0s;
  }
  
  .typing__dot:nth-child(1) {
    animation-delay: 0.2s;
  }
  
  .typing__dot:nth-child(1) {
    animation-delay: 0.4s;
  }
  
  @keyframes loadingFade {
    0% {
      opacity: 0;
    }
  
    50% {
      opacity: 50%;
    }
    100% {
      opacity: 0;
    }
  }
  
  /* emoji picker styles */
  
  .emojiPicker {
    position: absolute;
    font-size: large;
  }
  
  .skeleton {
    position: relative;
    overflow: hidden;
    height: 40px;
    margin-bottom: 10px;
  }
  
  .skeleton:after {
    content: '';
    display: block;
    position: absolute;
    top: 0;
    left: 0;
    width: 50%;
    height: 100%;
    animation: sweep 2s infinite;
    background-image: linear-gradient(
      to left,
      transparent,
      rgba(255, 255, 255, 0.4) transparent
    );
  }
  
  @keyframes sweep {
    0% {
      transform: translateX(-100%);
    }
  
    50% {
      transform: translateX(150%);
    }
  
    100% {
      transform: translateX(-100%);
    }
  }
  
  .skeleton__avatar {
    height: 35px;
    width: 35px;
    border-radius: 3px;
    background-color: rgba(58, 57, 57, 0.3);
  }
  
  .skeleton__author {
    background-color: rgba(58, 57, 57, 0.3);
    width: 120px;
    height: 10px;
    border-radius: 3px;
    position: absolute;
    bottom: 30px;
    left: 40px;
    right: 0;
  }
  
  .skeleton__details {
    background-color: rgba(58, 57, 57, 0.3);
    height: 20px;
    border-radius: 3px;
    position: absolute;
    bottom: 3px;
    right: 20px;
    left: 40px;
  }
  
  .chat {
    /* flex: 0.75; */
