  }
  
  .reactGiphySearchbox-poweredByGiphy > svg {
    height: 7px;
  }
  
  .reactGiphySearchbox-listWrapper {
    background: black !important;
  }
  
  .chat__input > form > input {
    padding: 15px;
    background: transparent;
    border: none;
    outline-width: 0;
    /* color: white; */
    width: 95%;
    font-size: large;
  }
  
  .chat__input > form {
    flex: 1;
  }
  
  .chat__sendMessage {
    display: none;
  }
  
  .chat__inputIcons > .MuiSvgIcon-root {
    padding: 5px;
  }
  
  .chat__messages {
    /* height: 73.8vh; */
    overflow-y: auto;
    scroll-snap-type: y mandatory;
  }
  
  .chat__messages > div:last-child {
    scroll-snap-align: end;
  }
  
  ::-webkit-scrollbar {
    width: 12px;
  }
  
  ::-webkit-scrollbar-track {
    background: #161b22;
    border-radius: 2px;
  }
  
  ::-webkit-scrollbar-thumb {
    background: #0d1016;
    border-radius: 20px;
    /* border: 3px solid #161B22; */
  }
  
  ::-webkit-scrollbar-thumb:hover {
    background: rgb(8, 8, 15);
  }